/** @jsxImportSource @emotion/react */
import React, { useEffect } from 'react'
import { HashRouter as Router, Switch, Route, Redirect, useHistory } from 'react-router-dom'
import { atom, useSetRecoilState, useRecoilValue } from 'recoil'
import log from 'loglevel'
import { BoxListing } from '../BoxListing'
import { BoxDetail } from '../BoxDetail'
import { SampleOverview } from '../SampleOverview'
import { SampleSorting } from '../SampleSorting'
import { SampleTaxonomyIdentification } from '../SampleTaxonomyIdentification'
import { AppHeader } from '../AppHeader'
import { SideNavBar } from '../SideNavBar'
import { ErrorBoundary } from '../ErrorBoundary'
import { ModelsListing } from '../ModelsListing'
import { PredictorsListing } from '../PredictorsListing'
import { CustomerSubmission } from '../CustomerSubmission'

interface NavigateFn {
  home: () => void
  boxes: () => void
  boxDetail: ({ boxId }: { boxId: number }) => void
  sampleOverview: ({ sampleId }: { sampleId: number }) => void
  sampleSorting: ({ sampleId }: { sampleId: number }) => void
  sampleTaxonomyIdentification: ({ sampleId }: { sampleId: number }) => void
  models: () => void
  predictors: () => void
}

const makeNavigateFn = (history: { push: (url) => void }): NavigateFn => ({
  home: (): void => history.push('/'),
  boxes: (): void => history.push('/boxes'),
  boxDetail: ({ boxId }: { boxId: number }): void => history.push(`/boxes/${boxId}`),
  sampleOverview: ({ sampleId }: { sampleId: number }): void => history.push(`/samples/${sampleId}`),
  sampleSorting: ({ sampleId }: { sampleId: number }): void => history.push(`/samples/${sampleId}/sorting`),
  sampleTaxonomyIdentification: ({ sampleId }: { sampleId: number }): void =>
    history.push(`/samples/${sampleId}/taxonomy`),
  models: (): void => history.push('/models'),
  predictors: (): void => history.push('/predictors'),
})

export const navigateState = atom({
  key: 'AppRouter/navigate',
  default: makeNavigateFn({ push: (url) => log.debug('navigate:', url) }),
})

export const useNavigate = (): NavigateFn => useRecoilValue(navigateState)

export const AppRouter = (): JSX.Element => {
  const setNavigate = useSetRecoilState(navigateState)
  const history = useHistory()

  useEffect(() => {
    setNavigate(() => makeNavigateFn(history))
  }, [])

  return (
    <div css={{ height: '100vh', display: 'flex', flexDirection: 'column' }}>
      <AppHeader />
      <SideNavBar />
      <div css={{ flex: 1, padding: '0.5em' }}>
        <ErrorBoundary>
          <Router>
            <Switch>
              <Route exact path="/">
                <Redirect to="/boxes" />
              </Route>
              <Route exact path="/boxes" render={() => <BoxListing />} />
              <Route exact path="/boxes/:boxId" render={({ match }) => <BoxDetail boxId={match.params.boxId} />} />
              <Route
                exact
                path="/samples/:sampleId"
                render={({ match }) => <SampleOverview sampleId={match.params.sampleId} />}
              />
              <Route
                exact
                path="/samples/:sampleId/sorting"
                render={({ match }) => <SampleSorting sampleId={match.params.sampleId} />}
              />
              <Route
                exact
                path="/samples/:sampleId/taxonomy"
                render={({ match }) => <SampleTaxonomyIdentification sampleId={match.params.sampleId} />}
              />
              <Route exact path="/models" render={() => <ModelsListing />} />
              <Route exact path="/predictors" render={() => <PredictorsListing />} />
              <Route exact path="/submission/new" render={() => <CustomerSubmission />} />
              <Route path="*">
                <h2>Page not found.</h2>
              </Route>
            </Switch>
          </Router>
        </ErrorBoundary>
      </div>
    </div>
  )
}
