/** @jsxImportSource @emotion/react */
import React, { useMemo } from 'react'
import { atom, useRecoilState } from 'recoil'
import Excel from 'exceljs'
import MuiStepper from '@mui/material/Stepper'
import MuiStep from '@mui/material/Step'
import MuiStepLabel from '@mui/material/StepLabel'
import MuiButton from '@mui/material/Button'
import KeyboardArrowLeftIcon from '@mui/icons-material/KeyboardArrowLeft'
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight'
import SendIcon from '@mui/icons-material/Send'
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline'
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline'

import { useUpdatableState } from '../../lib/useUpdatableState'
import { DataGrid } from '../DataGrid'
import { FileProblem, processFile, ProcessFileResult } from '../../lib/submissions'
import { problemsColumnDefs } from './columnDefs'
import { UploadFileBox } from './UploadFileBox'
import { isStagingApi } from '../../lib/environment'

// export interface CustomerSubmissionProps {}

export type ProcessingStatus = 'empty' | 'uploading' | 'processing' | 'processed' | 'submitting' | 'submitted'

export interface ProblemRow extends FileProblem {
  id: string
}

enum STEP {
  prepare = 0,
  upload = 1,
  review = 2,
  finished = 3,
}

const stepTitles = ['Prepare File', 'Upload File', 'Review & Submit', 'Finished']

const getProblemRowId = (() => {
  const idDict = {}
  let nextId = 1
  return (problem: FileProblem) => {
    const key = JSON.stringify(problem)
    if (!idDict[key]) idDict[key] = nextId++
    return idDict[key]
  }
})()

const expandJsonState = atom({
  key: 'CustomerSubmission/expandJsonState',
  default: true,
})

const emptyProcessResult = { samples: [], info: {}, problems: [] }
interface State {
  // current
  processingStatus: ProcessingStatus
  processResult: ProcessFileResult
  filename: string
  processingToken: number
  errorMessage: string

  // for revert
  lastProcessingStatus: ProcessingStatus
  lastFilename: string
}

const MessageBlock = (props) => (
  <div css={{ margin: '1em 0', fontWeight: 'bold', display: 'flex', alignItems: 'center' }} {...props} />
)

export const CustomerSubmission = (/* props: CustomerSubmissionProps */): JSX.Element => {
  const [expandJson, setExpandJson] = useRecoilState(expandJsonState)

  const [state, setState, updateState] = useUpdatableState<State>({
    processingStatus: 'empty',
    processResult: emptyProcessResult,
    filename: '',
    processingToken: 0,
    errorMessage: '',
    lastProcessingStatus: 'empty',
    lastFilename: '',
  })
  const [activeStep, setActiveStep] = React.useState<STEP>(STEP.prepare)

  const { processingStatus, processingToken, processResult, errorMessage, filename } = state

  const { samples, info, problems } = processResult

  const handleError = (message: string) => {
    updateState((draft) => {
      draft.processingStatus = 'empty'
      draft.processResult = emptyProcessResult
      draft.filename = ''
      draft.errorMessage = message
    })
  }

  const handleDrop = (acceptedFiles) => {
    console.log(acceptedFiles.length)
    if (acceptedFiles.length > 1) {
      handleError('Only a single file is allowed.')
      return
    }

    const file = acceptedFiles[0]
    const reader = new FileReader()
    const thisProcessingToken = processingToken + 1

    updateState((draft) => {
      draft.lastProcessingStatus = draft.processingStatus
      draft.processingStatus = 'uploading'
      draft.processingToken = thisProcessingToken
      draft.errorMessage = ''
    })

    reader.onerror = () => {
      handleError('Could not read file.')
    }
    reader.onload = async () => {
      updateState((draft) => {
        draft.processingStatus = 'processing'
        draft.lastFilename = draft.filename
        draft.filename = file.name
      })
      try {
        const buffer = reader.result
        if (!(buffer instanceof ArrayBuffer)) throw new Error('ArrayBuffer error')
        const wb = new Excel.Workbook()
        await wb.xlsx.load(buffer)

        const processResult = await processFile(wb)

        updateState((draft) => {
          if (draft.processingToken !== thisProcessingToken) return // this processing is aborted/stale
          draft.processingStatus = 'processed'
          draft.processResult = processResult
        })
      } catch (err) {
        handleError('Could not parse file.')
      }
    }
    try {
      reader.readAsArrayBuffer(file)
    } catch (err) {
      handleError('Could not read file.')
    }
  }

  const handleAbort = () => {
    updateState((draft) => {
      draft.processingStatus = draft.lastProcessingStatus
      draft.filename = draft.lastFilename
      draft.processingToken++
    })
  }

  const rows = useMemo(() => {
    return problems.map((problem) => ({ id: getProblemRowId(problem), ...problem }))
  }, [problems])

  const isReadyToSubmit = samples.length > 0 && problems.length === 0 && processingStatus === 'processed'

  const hasFatalProblem = processResult.problems.some(({ type }) => type === 'fatal')
  const hasOnlyRowProblems = !hasFatalProblem && processResult.problems.length > 0
  const hasNoSamples = processingStatus === 'processed' && processResult.samples.length === 0

  const uploadAttentionMessage = (() => {
    if (errorMessage) return <span>Error reading file. Did you choose the correct file? ({errorMessage})</span>
    if (hasFatalProblem)
      return <span>Error processing file. Did you choose the correct file? ({processResult.problems[0].message})</span>
    if (hasOnlyRowProblems)
      return <span>Correct the following {processResult.problems.length} problems and try uploading again:</span>
    if (hasNoSamples) return <span>File does not contain any samples.</span>
    return null
  })()

  return (
    <div css={{ height: '100%', display: 'flex', flexDirection: 'column', maxWidth: '70rem' }}>
      <MuiStepper activeStep={activeStep}>
        {stepTitles.map((stepTitle) => (
          <MuiStep key={stepTitle}>
            <MuiStepLabel>{stepTitle}</MuiStepLabel>
          </MuiStep>
        ))}
      </MuiStepper>
      <div css={{ display: 'flex', alignItems: 'center', margin: '2em 0' }}>
        <div>
          <MuiButton
            variant="outlined"
            onClick={() => setActiveStep((s) => s - 1)}
            disabled={activeStep === STEP.prepare || activeStep === STEP.finished}
          >
            <KeyboardArrowLeftIcon />
            Back
          </MuiButton>
        </div>
        <div css={{ flex: 1, textAlign: 'center', fontSize: '125%' }}>
          Step {activeStep + 1}: {stepTitles[activeStep]}
        </div>
        <div>
          {activeStep < STEP.review && (
            <MuiButton
              variant="outlined"
              onClick={() => setActiveStep((s) => s + 1)}
              disabled={activeStep === STEP.upload && !isReadyToSubmit}
            >
              Next
              <KeyboardArrowRightIcon />
            </MuiButton>
          )}
          {activeStep >= STEP.review && (
            <MuiButton
              variant={activeStep === STEP.finished ? 'outlined' : 'contained'}
              onClick={() => setActiveStep((s) => s + 1)}
              disabled={!isReadyToSubmit || activeStep === STEP.finished}
            >
              Submit&nbsp;
              <SendIcon />
            </MuiButton>
          )}
        </div>
      </div>
      <div css={{ flex: 1 }}>
        {activeStep === STEP.prepare && (
          <div>
            <p>[template and download instructions]</p>
            <div>
              <MuiButton disabled>Download Template</MuiButton>
            </div>
          </div>
        )}
        {activeStep === STEP.upload && (
          <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
            <UploadFileBox
              onDrop={handleDrop}
              onCancel={handleAbort}
              processingStatus={processingStatus}
              filename={filename}
            />
            {uploadAttentionMessage && (
              <MessageBlock>
                <ErrorOutlineIcon />
                &nbsp;{uploadAttentionMessage}
              </MessageBlock>
            )}
            {isReadyToSubmit && (
              <MessageBlock>
                <CheckCircleOutlineIcon />
                &nbsp;Ready to review. Click Next to continue.
              </MessageBlock>
            )}
            {hasOnlyRowProblems && (
              <div css={{ flex: 1 }}>
                <DataGrid<ProblemRow> columnDefs={problemsColumnDefs} rows={rows} />
              </div>
            )}
          </div>
        )}
        {activeStep === STEP.review && (
          <div>
            <table css={{ borderCollapse: 'collapse', 'th,td': { padding: '0.75em', border: '1px solid grey' } }}>
              <thead>
                <th>Sample Type</th>
                <th># Samples</th>
              </thead>
              <tbody>
                {Object.entries(info).map(([wsName, sampleCount]) => (
                  <tr key={wsName}>
                    <td>{wsName}</td>
                    <td css={{ textAlign: 'right' }}>{sampleCount}</td>
                  </tr>
                ))}
              </tbody>
            </table>
            <MessageBlock>
              <CheckCircleOutlineIcon />
              &nbsp;Ready to submit. Click Submit to finish.
            </MessageBlock>
            {isStagingApi && (
              <div>
                <MuiButton
                  onClick={() => {
                    setExpandJson((v) => !v)
                  }}
                >
                  {expandJson ? 'Hide JSON' : 'Show JSON'}
                </MuiButton>
                {expandJson && <pre>{JSON.stringify(samples, null, 2)}</pre>}
              </div>
            )}
          </div>
        )}
        {activeStep === STEP.finished && (
          <>
            <div>[done messaging, link to submission detail]</div>
          </>
        )}
      </div>
    </div>
  )
}
