import gql from 'graphql-tag'
import {
  TaxaLevelLookup as GraphTaxaLevelLookup,
  LifeStageLookup as GraphLifeStageLookup,
  NoteTypes as GraphNoteTypes,
  BoxStates as GraphBoxStates,
} from '@namcbugdb/common'

import { Api } from '../api'
import { LifeStage, NoteType, TaxaLevel, BoxState } from '../../types'

export const fetchLookupsQuery = gql`
  query {
    lookups {
      taxa {
        lifeStages
        taxaLevels
      }
      sample {
        noteTypes
        boxStates
      }
    }
  }
`

export const fetchLookups =
  (api: Api) =>
  async (): Promise<{
    taxaLevels: TaxaLevel[]
    lifeStages: LifeStage[]
    noteTypes: NoteType[]
    boxStates: BoxState[]
  }> => {
    const data = await api.query<{
      lookups: {
        taxa: {
          taxaLevels: GraphTaxaLevelLookup[]
          lifeStages: GraphLifeStageLookup[]
        }
        sample: {
          noteTypes: GraphNoteTypes[]
          boxStates: GraphBoxStates[]
        }
      }
    }>(fetchLookupsQuery)
    const {
      lookups: {
        taxa: { taxaLevels, lifeStages },
        sample: { noteTypes, boxStates },
      },
    } = data

    return {
      taxaLevels,
      lifeStages,
      noteTypes,
      boxStates,
    }
  }
