/* eslint-disable @typescript-eslint/no-non-null-assertion */
/** @jsxImportSource @emotion/react */
import React, { useRef, useMemo } from 'react'
import { useHotkeys } from 'react-hotkeys-hook'
import { useRecoilState, atom } from 'recoil'
// import log from 'loglevel'

import { useDebounce } from '../../lib/useDebounce'
import { DataGrid, renderers as commonRenderers } from '../DataGrid'
import { Box, BoxState } from '../../types'
import { boxesColumnDefs } from './columnDefs'
import { TextField } from '../TextField'
import { Chip } from '../Chip'
import { SelectField } from '../SelectField'
import * as componentRenderers from './renderers'
import { formatNumber } from '../../lib/formats'
import { useNavigate } from '../AppRouter'

const renderers = { ...commonRenderers, ...componentRenderers }

export interface BoxListingProps {
  boxes: Box[]
  boxStates: BoxState[]
}

export interface BoxRow extends Box {
  boxId: {
    label: number
    onClick: () => void
  }
  customer: {
    label: string
    onFilter: () => void
    disabled: boolean
  }
}

const gridOptions = {
  suppressCellSelection: true,
}

interface Filter {
  id: string
  label: string
  textColor?: string
  chipColor?: string
  onDelete?: () => void
  filterFn: (box: Box) => boolean
}

const ANY_BOX_STATE = 'ANY'
const ACTIVE_BOX_STATE = 'Active'
const FILTER_CHIP_COLOR = 'lightblue'

const boxStateNameFilterState = atom({
  key: 'BoxListing/boxStateNameFilter',
  default: ACTIVE_BOX_STATE,
})

const textFilterState = atom({
  key: 'BoxListing/textFilter',
  default: '',
})

const addedFiltersState = atom<Filter[]>({
  key: 'BoxListing/addedFilters',
  default: [],
})

export const BoxListing = ({ boxes, boxStates }: BoxListingProps): JSX.Element => {
  const navigate = useNavigate()

  // STATE AND REFS

  const [boxStateNameFilter, setBoxStateNameFilter] = useRecoilState(boxStateNameFilterState)
  const [textFilter, setTextFilter] = useRecoilState(textFilterState)
  const [addedFilters, setAddedFilters] = useRecoilState(addedFiltersState)
  const [textFilterDebounced, setTextFilterDebounced] = useDebounce(textFilter, 500)
  const resetTextFilter = () => {
    setTextFilter('')
    setTextFilterDebounced('')
  }
  const textFilterRef = useRef<HTMLInputElement>(null)

  // HOTKEYS

  useHotkeys('cmd+f, ctrl+f', (e) => {
    e.preventDefault()
    e.stopPropagation()
    textFilterRef.current?.focus()
  })

  // FILTERS

  const filters = useMemo(() => {
    const result: Filter[] = []

    if (boxStateNameFilter !== ANY_BOX_STATE) {
      result.push({
        id: 'state',
        label: boxStateNameFilter,
        chipColor: FILTER_CHIP_COLOR,
        filterFn: (box) => box.boxState === boxStateNameFilter,
        onDelete: () => {
          setBoxStateNameFilter(ANY_BOX_STATE)
        },
      })
    }

    const textFilterTrimmed = textFilterDebounced.trim()
    if (textFilterTrimmed) {
      if (textFilterTrimmed.match(/^\d+$/)) {
        const targetBoxId = Number(textFilterTrimmed)
        result.push({
          id: 'text',
          label: `Box ${textFilterTrimmed}`,
          chipColor: FILTER_CHIP_COLOR,
          filterFn: (box) => box.id === targetBoxId,
          onDelete: resetTextFilter,
        })
      } else {
        result.push({
          id: 'text',
          label: `Customer contains "${textFilterTrimmed}"`,
          chipColor: FILTER_CHIP_COLOR,
          filterFn: ({ customerName }) => customerName.toLowerCase().includes(textFilterTrimmed.toLowerCase()),
          onDelete: resetTextFilter,
        })
      }
    }

    result.push(...addedFilters)

    if (result.length === 0) {
      result.push({
        id: 'none',
        label: 'No filters',
        filterFn: () => true,
      })
    }

    return result
  }, [boxStateNameFilter, textFilterDebounced, addedFilters])

  const rows = useMemo<BoxRow[]>(() => {
    const filtered = filters.reduce<Box[]>((acc, { filterFn }) => acc.filter(filterFn), boxes)
    return filtered.map((box) => {
      const filterId = `filter/${String(box.customerId)}`
      return {
        ...box,
        boxId: {
          label: box.id,
          onClick: () => navigate.boxDetail({ boxId: box.id }),
        },
        customer: {
          label: box.customerName,
          onFilter: () => {
            resetTextFilter()
            setAddedFilters([
              {
                id: filterId,
                label: box.customerName,
                chipColor: FILTER_CHIP_COLOR,
                filterFn: ({ customerId }) => customerId === box.customerId,
                onDelete: () => setAddedFilters((value) => value.filter(({ id }) => id !== filterId)),
              },
            ])
          },
          disabled: !!addedFilters.find(({ id }) => id === filterId),
        },
      }
    })
  }, [boxes, filters])

  // RENDER

  const boxStateOptions = useMemo(() => {
    return [
      { value: ANY_BOX_STATE, label: 'Any' },
      ...boxStates.filter(({ isActive }) => isActive).map(({ name }) => ({ value: name, label: name })),
    ]
  }, [boxStates])

  return (
    <>
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <div css={{ display: 'flex', gap: '0.5em' }}>
          <SelectField
            value={boxStateNameFilter}
            onChange={(value) => setBoxStateNameFilter(String(value))}
            options={boxStateOptions}
            label="Box State"
          />
          <TextField
            css={{ width: '30em' }}
            value={textFilter}
            onChange={setTextFilter}
            label="Box ID or Customer"
            inputRef={textFilterRef}
          />
        </div>
        <div css={{ minHeight: '2em', marginTop: '0.75em', display: 'flex' }}>
          <div css={{ flex: 1, display: 'flex', gap: '0.5em', alignItems: 'center', fontSize: '0.8125rem' }}>
            {filters.map(({ id, label, textColor, chipColor, onDelete }, index) => (
              <React.Fragment key={id}>
                {!!index && <span css={{ fontStyle: 'italic', opacity: 0.6 }}> and </span>}
                <Chip label={label} textColor={textColor} chipColor={chipColor} onDelete={onDelete} />
              </React.Fragment>
            ))}
          </div>
          <div
            css={{
              fontSize: '0.8125rem' /* match chip */,
              display: 'flex',
              alignItems: 'center',
            }}
          >
            {rows.length === boxes.length
              ? `${formatNumber(rows.length)} boxes`
              : `${formatNumber(rows.length)} of ${formatNumber(boxes.length)} boxes shown`}
          </div>
        </div>
        <div css={{ flex: 1 }}>
          <DataGrid<BoxRow>
            rows={rows}
            columnDefs={boxesColumnDefs}
            frameworkComponents={renderers}
            // context={context}
            gridOptions={gridOptions}
            // onGridApisReady={setGridApis}
          />
        </div>
      </div>
    </>
  )
}
