import React, { Suspense } from 'react'
import { useRecoilValue } from 'recoil'

import { selectBoxes, selectBoxStates } from '../../data/selectors'
import { BoxListing as Component } from './BoxListing'
import { Fallback } from './Fallback'

const Connector = (): JSX.Element => {
  const boxes = useRecoilValue(selectBoxes)
  const boxStates = useRecoilValue(selectBoxStates)

  return <Component boxes={boxes} boxStates={boxStates} />
}

export const BoxListing = (): JSX.Element => {
  return (
    <Suspense fallback={<Fallback />}>
      <Connector />
    </Suspense>
  )
}
