import Excel from 'exceljs'
import dayjs from 'dayjs'
import utc from 'dayjs/plugin/utc'

import { KnownError, Cell } from './excel'

export type ProcessorFn = (cell: Cell, config?: { lists: Record<string, StringValueList> }) => void

export type StringValueList = { descriptor: string; values: string[] }

dayjs.extend(utc)

export const isRequired: ProcessorFn = (cell) => {
  if (cell.value === undefined || cell.value === null) throw new KnownError({ message: 'Cell is required' })
}

export const isNumber: ProcessorFn = (cell) => {
  if (!cell.value) return
  if (typeof cell.value !== 'number') throw new KnownError({ message: 'Cell must be a number' })
}

export const isWholeNumber: ProcessorFn = (cell) => {
  if (!cell.value) return
  isNumber(cell)
  if (cell.value !== Math.trunc(Number(cell.value))) throw new KnownError({ message: 'Cell must be a whole number' })
}

export const isNumberBetween =
  (min: number, max: number): ProcessorFn =>
  (cell) => {
    if (!cell.value) return
    isNumber(cell)
    if (cell.value < min || cell.value > max)
      throw new KnownError({ message: `Cell must be a number between ${min} and ${max}` })
  }

export const isPositiveNumber: ProcessorFn = (cell) => {
  if (!cell.value) return
  isNumber(cell)
  if (cell.value <= 0) throw new KnownError({ message: `Cell must be a number greater than 0` })
}

export const isPercent: ProcessorFn = (cell) => {
  if (!cell.value) return
  isNumber(cell)
  if (cell.value < 0 || cell.value > 1) throw new KnownError({ message: `Cell must be a percentage` })
}

export const isInStaticList =
  (list: StringValueList): ProcessorFn =>
  (cell) => {
    if (!cell.value) return
    if (!list.values.includes(String(cell.value)))
      throw new KnownError({ message: `Cell must be a valid ${list.descriptor}` })
  }

export const isInDynamicList =
  (listKey: string): ProcessorFn =>
  (cell, config) => {
    if (!cell.value) return
    if (!config || !config.lists[listKey]) throw new Error(`Missing list "${listKey}"`)
    const list = config.lists[listKey]
    if (!list.values.includes(String(cell.value)))
      throw new KnownError({ message: `Cell must be a valid ${list.descriptor}` })
  }

export const isAcceptableDate: ProcessorFn = (cell) => {
  const MIN_DATE = '2000-01-01'
  if (!cell.value) return
  if (!(cell.value instanceof Date)) throw new KnownError({ message: 'Cell must be a date' })
  if (dayjs(cell.value).isBefore(MIN_DATE)) throw new KnownError({ message: `Cell must be a date after ${MIN_DATE}` })
}

export const toShortDateString: ProcessorFn = (cell) => {
  if (!(cell.value instanceof Date)) throw new KnownError({ message: 'Cell must be a date' })
  cell.value = dayjs.utc(cell.value).format('YYYY-MM-DD')
}

export const toString: ProcessorFn = (cell) => {
  cell.value = cell.value && String(cell.value)
}

export const setValue =
  (value: Excel.CellValue): ProcessorFn =>
  (cell) => {
    cell.value = value
  }

export const isReadable: ProcessorFn = (cell) => {
  if (
    !(
      typeof cell.value === 'string' ||
      typeof cell.value === 'number' ||
      typeof cell.value === 'boolean' ||
      typeof cell.value === 'undefined' ||
      cell.value instanceof Date ||
      cell.value === null
    )
  )
    throw new KnownError({ message: 'Cell must be a simple value (e.g. no formulas)' })
}

export const trimIfString: ProcessorFn = (cell) => {
  if (typeof cell.value !== 'string') return
  cell.value = cell.value.trim()
}
