/** @jsxImportSource @emotion/react */
import React from 'react'
import { useDropzone } from 'react-dropzone'
import MuiLinearProgress from '@mui/material/LinearProgress'
import UploadFileIcon from '@mui/icons-material/UploadFile'
import FilePresentIcon from '@mui/icons-material/FilePresent'
import MuiButton from '@mui/material/Button'

import { ProcessingStatus } from './CustomerSubmission'

export interface UploadFileBoxProps {
  onDrop: (files: any[]) => void
  onCancel: () => void
  processingStatus: ProcessingStatus
  filename: string
}

export const UploadFileBox = ({ onDrop, onCancel, processingStatus, filename }: UploadFileBoxProps): JSX.Element => {
  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
  })

  const isPerforming = processingStatus === 'uploading' || processingStatus === 'processing'

  return (
    <div
      {...(isPerforming ? {} : getRootProps())}
      css={{
        border: isDragActive ? '3px dashed blue' : '3px dashed lightgrey',
        minHeight: '6em',
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        cursor: isPerforming ? 'default' : 'pointer',
        position: 'relative',
      }}
    >
      {isPerforming ? (
        <div css={{ width: '80%' }}>
          <MuiLinearProgress />
          <div css={{ textAlign: 'center' }}>
            <MuiButton onClick={onCancel}>Cancel</MuiButton>
          </div>
        </div>
      ) : (
        <div css={{ display: 'flex', width: '80%', minHeight: '3em' }}>
          <div
            css={{
              flex: 1,
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              borderRight: '1px solid grey',
              paddingRight: '1em',
              textAlign: 'center',
            }}
          >
            {filename ? (
              <>
                <FilePresentIcon /> {filename}
              </>
            ) : (
              <>
                <UploadFileIcon /> No file
              </>
            )}
          </div>
          <div
            css={{
              flex: 1,
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              paddingLeft: '1em',
              textAlign: 'center',
            }}
          >
            <span>
              Drop here or&nbsp;<a>choose file</a>
            </span>
          </div>
        </div>
      )}
      <input {...getInputProps()} />
    </div>
  )
}
