import gql from 'graphql-tag'
import { User as GraphUser, UserPermissions } from '@namcbugdb/common'

import { Api } from '../api'
import { User } from '../../types'

export const fetchMyUserQuery = gql`
  query {
    myProfile {
      id
      firstName
      lastName
      email
      permissions
    }
  }
`

export const fetchMyUser = (api: Api) => async (): Promise<User & { permissions: UserPermissions }> => {
  const data = await api.query<{
    myProfile: GraphUser
  }>(fetchMyUserQuery)
  const { myProfile } = data

  return {
    ...myProfile,
    permissions: myProfile.permissions || {},
    fullName: `${myProfile.firstName} ${myProfile.lastName}`,
  }
}
