import log from 'loglevel'

import { FileSample, FileProblem } from './excel'
import { SheetSchema } from './templateSchema'

// TODO: use API
const getUsStateFromCoordinates = async ({ latitude, longitude }: { latitude: number; longitude: number }) =>
  new Promise((resolve) => {
    log.debug(`Mock checking US state at ${latitude}, ${longitude}`)
    window.setTimeout(() => {
      if (!latitude || !longitude) {
        resolve(false)
        return
      }
      resolve(true)
    }, 1000)
  })

export const processParsedSample = async (
  sourceSample: FileSample,
  schema: SheetSchema
): Promise<{ sample: FileSample; problems: FileProblem[] }> => {
  const sample = { ...sourceSample }
  const problems: FileProblem[] = []

  // add fields
  const addFieldNames = ['sampleType']
  addFieldNames.forEach((fieldName) => {
    const virtualCell = { value: '' }
    const processors = schema[fieldName]
    if (!processors) throw new Error(`Missing essential field "${fieldName}"`)
    processors.forEach((fn) => fn(virtualCell)) // process virtual value
    sample[fieldName] = virtualCell.value
  })

  // split ecosystemHabitat into ecosystem, habitat
  if (typeof sample.ecosystemHabitat === 'string') {
    const [ecosystemPart, habitatPart] = sample.ecosystemHabitat.split(':')
    sample.ecosystem = ecosystemPart.trim()
    sample.habitat = habitatPart.trim()
    delete sample.ecosystemHabitat
  }

  // validate coordinates
  const { latitude, longitude } = sample
  if (typeof latitude === 'number' && typeof longitude === 'number') {
    const usState = await getUsStateFromCoordinates({ latitude, longitude })
    if (!usState) {
      problems.push({ type: 'row', message: 'Latitude/Longitude is outside of accepted area.' })
    }
  }

  return { sample, problems }
}
