/** @jsxImportSource @emotion/react */
import React from 'react'
import { ICellRendererParams } from 'ag-grid-community'
import IconButton from '@mui/material/IconButton'
import FilterAltIcon from '@mui/icons-material/FilterAlt'

import { withTooltip } from '../withTooltip'
import { BoxRow } from './BoxListing'

interface CellRendererParams<T> extends ICellRendererParams {
  data: BoxRow
  value: T
}

export const FilterableTextRenderer = ({
  value: { label, onFilter, disabled },
}: CellRendererParams<{
  label: string | number
  onFilter: () => void
  disabled?: boolean
}>): React.ReactNode => {
  return (
    <div>
      <div css={{ position: 'absolute', right: 0 }}>
        <IconButton
          onClick={() => {
            onFilter()
          }}
          disabled={disabled}
        >
          {withTooltip(<FilterAltIcon />, 'Filter by this customer')}
        </IconButton>
      </div>
      {label}
    </div>
  )
}
